<!-- author: 刘思涵 -->
<template>
  <div id="aunt_list">
    <el-row>
      <el-col>
        <el-card class="card-box pt-0" shadow="hover">
          <el-form :inline="true" >
            <el-form-item label="阿姨姓名" >
              <el-input v-model="searchName" placeholder="输入阿姨姓名" style="width:200px"></el-input>
            </el-form-item>
            <el-form-item label="手机号码">
              <el-input v-model="searchPhone" placeholder="输入手机号码" style="width:200px"></el-input>
            </el-form-item>
            <el-form-item label="服务类型">
              <el-select v-model="typeValue" placeholder="全部" @change="changeType" style="width:200px">
                <el-option
                  v-for="item in optionsType"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="上户状态">
              <el-select v-model="statusValue" placeholder="全部" style="width:200px">
                <el-option
                  v-for="item in optionsStatus"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <br/>
            <el-form-item>
              <el-button type="primary" @click="search">查询</el-button>
              <el-button @click="clear">清除查询</el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <el-card class="card-box" shadow="hover">
          <div class="table-wrap" ref="table">
            <div class="over-table">
              <span class="count" style="float:left">数据统计：共{{total}}个</span>
              <div class="btn-group" style="width:100px">
                <el-button type="primary" @click="addAunt" >添加阿姨</el-button>
              </div>
            </div>
            <el-scrollbar >
              <el-table :data="tableData" stripe border fit header-row-class-name="table-head" style="width:100%;height:100%" >
              <el-table-column prop="id" label="ID"  ></el-table-column>
              <el-table-column prop="title" label="阿姨姓名" style="width:50px;height:100px"></el-table-column>
              <el-table-column prop="phone" label="手机号码">
              </el-table-column>
              <el-table-column label="阿姨头像">
                <template slot-scope="scope">
                  <div>
                    <img :src="scope.row.photo" style="width:100%;height:90px;object-fit: cover;"/>
                  </div>
                </template>
              </el-table-column>
              <el-table-column label="录入时间">
                <template slot-scope="scope">
                  <div>
                    <p>{{scope.row.create_time}}</p>
                  </div>
                </template>
              </el-table-column>
              <el-table-column label="服务类型" prop="type">
              </el-table-column>
              <el-table-column label="服务薪资" prop="salary">
              </el-table-column>
              <el-table-column label="服务订单数" prop="num">
              </el-table-column>
              <el-table-column label="服务状态" prop="service_status">
              </el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button @click="getDetail(scope.row)" type="text" size="small">查看</el-button>
                  <el-button style="margin-left: 0px;" @click="updateStatus(scope.row, '0')" type="text" v-if="scope.row.off_shelve === 1" size="small">上架</el-button>
                  <el-button style="margin-left: 0px;" @click="updateStatus(scope.row, '1')" type="text" v-if="scope.row.off_shelve === 0" size="small">下架</el-button>
                </template>
              </el-table-column>
            </el-table>
            </el-scrollbar>
            <el-pagination background layout="prev, pager, next" :total="total" prev-text="上一页" next-text="下一页" @current-change="pageChange">
            </el-pagination>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { getAuntListService, getTypeService, setBlackService, getStatusService, setShelveStatus } from '../../service/AuntManage'
export default {
  data () {
    return {
      searchName: '',
      searchPhone: '',
      optionsType: [],
      optionsStatus: [],
      statusValue: '',
      optionsAffiliatedUnit: [],
      typeValue: '',
      affiliatedUnitValue: '',
      tableData: [],
      page: 1,
      per_page: 10,
      total: 0
    }
  },
  created () {
    this.$store.dispatch('commitChangeTopTitles', ['阿姨管理', '阿姨信息'])
    console.log(JSON.parse(sessionStorage.userInfo).id)
    this.getTypeOption()
    this.getStatusOption().then(() => {
      this._getAuntList()
    })
  },
  methods: {
    getStatusOption () {
      return new Promise((resolve, reject) => {
        getStatusService().then((res) => {
          console.log(res)
          let statusTempArray = []
          let resStatusobj = res.data
          for (const key in resStatusobj) {
            console.log(key)
            let obj = {}
            obj.label = resStatusobj[key]
            obj.value = key
            statusTempArray.push(obj)
          }
          console.log(statusTempArray)
          this.optionsStatus = statusTempArray
          resolve(res)
        })
      })
    },
    changeType (param) {
      this.optionsType.forEach((item) => {
        if (item.id === param) {
          this.typeValue = item.id
        }
      })
    },
    updateStatus (row, status) {
      console.log(status)
      let sendParams = {
        shelve_status: parseFloat(status),
        service_id: row.id
      }
      setShelveStatus(sendParams).then((res) => {
        if (res.status === 'success') {
          this.$message({type: 'success', message: `${sendParams.shelve_status === 0 ? '上' : '下'}架成功`})
          this._getAuntList()
        }
      })
    },
    getTypeOption () {
      getTypeService().then((res) => {
        console.log(res)
        let resTypeArray = res.data.items
        resTypeArray.forEach(element => {
          element.value = element.id
          element.label = element.title
        })
        let serveTypeOptions = resTypeArray
        this.optionsType = serveTypeOptions
      })
    },
    appBlacklist (param) {
      console.log(param)
      let sendParam = {}
      param.service_id = param.id
      param.blacklist_status = 1
      setBlackService(sendParam).then((res) => {
        this._getAuntList()
      })
    },
    pageChange (page) {
      console.log(page)
      this.page = page
      this._getAuntList()
    },
    _getAuntList () {
      // 获取阿姨详情
      let sendParam = `per_page=${this.per_page}&page=${this.page}&self=0&service_type_id=${this.typeValue}&blackstatus=0&service_status=${this.statusValue}&title=${this.searchName}&phone=${this.searchPhone}&&company_id=${JSON.parse(sessionStorage.userInfo).company_id}`
      // if (this.affiliatedUnitValue !== '') {
      //   sendParam = sendParam + '&company_id=' + this.affiliatedUnitValue
      // }
      getAuntListService(sendParam).then((res) => {
        console.log(res)
        let resData = res.data.items
        resData.forEach(element => {
          // element.type = element.service_type.title
          console.log(this.optionsStatus)
          var ar = this.optionsStatus.find((item) => {
            if (item.value === element.service_status) {
              return item.label
            }
          })
          console.log(ar)
          if (ar) {
            element.service_status = ar.label
          }
          if (element.service_type) {
            element.type = element.service_type.title
          }
        })
        this.tableData = resData
        console.log(this.data)
        this.$forceUpdate()
        this.total = res.data.total
      })
    },
    search () {
      this._getAuntList()
    },
    clear () {
      this.searchName = ''
      this.searchPhone = ''
      this.statusValue = ''
      this.affiliatedUnitValue = ''
      this.typeValue = ''
      this._getAuntList()
    },
    getDetail (obj) {
      this.$router.push('/AuntDetail/' + obj.id)
    },
    addAunt () {
      this.$router.push('/createAunt/-1')
    }
  }
}
</script>
<style scoped>
@import '../../assets/styles/common.css';
.el-pagination{
  margin-top: 20px;
  text-align: center;
}
.over-table{
  overflow: hidden;
  margin-bottom: 20px
}
.btn-group{
  float: right;
}
el-table-column {
  background-color: red;
}
</style>



